/* modulo 42 - pagina de Checkout - criando  a base da pagina de checkout*/
import React, { Component } from 'react';

import FormRadio from '../../components/Inputs/FormRadio';

class DadosPagamento extends Component {

	renderOpcoesPagamento() {
		
		return (
			<div className='flex horizontal'>
				<div className='flex-1'>
					<FormRadio name='PAC' checked={true} label='PAC (8 dias úteis) -  R$ 18,90' />
				</div>
				<div className='flex-1'>
					<FormRadio name='SEDEX' checked={true} label='SEDEX (3 dias úteis) -  R$ 35,90' />
				</div>
			</div>
		);

	}

	render() {
		return (
			<div className='Dados-Frete'>
				<h2>DADOS DE PAGAMENTO</h2>
				<br />

				{this.renderOpcoesPagamento()}

			</div>
		);
	}
}

export default DadosPagamento;